header {
  height: 100vh;
  background-image: url("../../assets/swapnil-dwivedi-Xcmfp0gBukI-unsplash.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
}

.hederInfo {
  width: 100%;
}

.headerContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.headerContent,
.chevronDown,
.infoButton {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.infoButton button {
  background-color: transparent;
  border: none;
  i:hover,
  i:focus {
    color: $green;
  }
}

.headerContent h1 {
  font-size: 10rem;
  font-weight: 900;
  text-align: center;
  text-shadow: 0 0 1rem;
  color: white;
}

.fa-info-circle {
  font-size: 5rem;
  color: $white;
  margin-bottom: 1px;
}
.runButton {
  margin: 0 0 100px 0;
}
.goRun {
  display: flex;
  margin: 22px auto 0 auto;
  justify-content: center;
  align-items: center;
  background-color: $red;
  color: $white;
  border: 2px solid $white;
}

.dropdown {
  position: relative;
}

.dropdownContent {
  display: none;
  position: absolute;
  bottom: 110%;
  border-radius: 10px;
  background-color: $white;
  margin: 0 auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 16px 10px;
  z-index: 1;
  font-size: 1.7rem;
  line-height: 1.4;
  text-align: center;
}

.dropdownContent p {
  padding: 5px 5px;
}

.dropdownContent,
.dropdownContent {
  display: block;
  border: 2px solid $red;
  transition: ease 0.6s;
  background-color: #fffe93;
  color: #d66761;
}
